body {
	font-family: 'Open Sans', sans-serif;
}

body, html {
	overflow-x: hidden;
}

.paddings {
	padding: 8px 16px;
}

.align-right {
	text-align: right;
	width: 100%;
}

.div-center {
    display: table;
    margin: 0 auto;
}

.div-center img {
    width: 10rem;
}

.buttons-group {
    display: flex;
}

.buttons-group button:first-child {
    margin-right: 8px;
}


.big-value {
	font-size: 7em;
	transition: font-size .1s;
	user-select: none;
}

.big-value.middle-size {
	font-size: 5em;
}

.big-value.middle-small-size {
	font-size: 4em;
}

.big-value.small-size {
	font-size: 3em;
	word-break: break-all;
}

.align-center {
	display: flex;
	align-items: center;
}

.justify-center {
	display: flex;
	justify-content: center;
}

.justify-center > div {
	flex: 1;
}

.button-full-width {
	display: flex;
	justify-content: flex-end;
}

.Button.full-widthi-right .Button__content {
	display: flex;
	justify-content: flex-end;
}

.button-full-width .CellButton__in {
	width: 100%;
	display: flex;
}

.button-full-width .CellButton__in .CellButton__before {
	flex: 1;
}

.button-full-width .CellButton__in .CellButton__content {
	flex: 10;
	display: flex;
	/*justify-content: flex-end;*/
}

.active-counter--wrap .Panel__in {
	display: flex;
	height: 100%;
	box-sizing: border-box;
}

.active-counter {
	display: flex;
	flex-direction: column;
	justify-content: flex-end;
	height: 100%;
	flex: 1;
}

.active-counter--value {
	flex: 3;
	display: flex;
	align-items: flex-end;
	text-align: center;
}

.active-counter-controllers {
	display: flex;
	flex-direction: column;
	width: 100%;
	box-sizing: border-box;
	flex: 3;
}

.active-counter-controllers .Button {
	display: block;
	width: 100%;
	margin-top: 6px;
	margin-bottom: 6px;
	flex: 1;
	border-radius: 3px;
	box-shadow: 0px 2px 2px 0px rgba(0,0,0,.1);
	transition: box-shadow .02s;
	align-self: stretch;
}

.active-counter-controllers .Touch {
	flex: 1;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
}


.active-counter-controllers .Touch .Button {
	align-self: stretch;
}

.active-counter-controllers .Button:active {
	/*box-shadow: 0px 2px 6px 3px rgba(0,0,0,.2);*/
}

.active-counter-controllers .Button .Button__content {
	font-size: 4em;
	/*font-weight: bold;*/
}

.active-counter-controllers .Button.minus {
	/*max-height: 58px;*/
}

.active-counter-controllers .Button.minus .Button__content {
	font-size: 3em;
}

.Panel__in, .FixedLayout {
	/*background-color: #fff !important;*/
	/*color: #000;*/
}

