body[scheme="client_dark"] .FixedLayout {
	background-color: #19191A !important;
	color: #fff;
}*/

.margins-top {
	margin-top: 8px;
}

.margins-top--bigger {
	margin-top: 22px;
}

.fixed-content {
	margin-top: 49px;
}

.fixed-content {
	margin-top: 63px;
}

.List--ios::before {
	/*z-index: 3;*/
}

.destructive-button-tertiary {
	color: #E64646;
}

.default-button-tertiary {
	color: var(--accent);
}

.controls .Button {
	padding: 8px 16px;
	min-height: 56px;
}

.controls .Button .Button .Button__content {
	padding: 0px 0px;
}

.controls,.counter-cell {
	background-color: var(--background_content);
}

.controls {
	align-items: center;
	padding: 3px 0px;
}

.Cell--dragging {
	z-index: 122 !important;
}

.Cell {
	position: relative;
	z-index: 1;
}

/*body[scheme="client_dark"] .default-button-tertiary, 
body[scheme="space_gray"] .default-button-tertiary {
	color: #71AAEB !important;
} */

.description {
	font-size: 14px;
	color: rgb(153,162,173);
}

.PanelHeader-content {
	padding-right: 90px;
}

.Button {
	cursor: pointer;
}

.Button.icon-button {
	background-color: var(--page_background);
	color: var(--accent);
	border-radius: 3px;
	overflow: hidden;
	padding: 4px 16px;
}

@media (max-height: 430px) {
	.active-counter {
		flex-direction: row;
	}

	.active-counter--value {
		align-items: center;
	}
}

@media (max-height: 295px) {
	.active-counter-controllers .Button,
	.active-counter-controllers .Button .Button__in {
		max-height: 58px;
	}
	.active-counter-controllers {
		justify-content: center;
		align-items: center;
	}
}

@media (min-width: 600px) {
	.active-counter {
		flex-direction: row;
		flex: 1;
	}
	.active-counter--value {
		flex: 6;
	}
	.active-counter--value {
		align-items: center;
	}
	.active-counter-controllers {
		justify-content: center;
		align-items: center;
	}
}

@media (max-height: 430px) {
	.big-value-small {
		font-size: 5em;
	}

	.big-value.middle-size {
		font-size: 3em;
	}

	.big-value.small-size {
		font-size: 2em;
	}
}
